import React from 'react'
import Link from 'gatsby-link'
import styled from 'styled-components'

const Button = styled(Link)`
  display: block;
  position: relative;
  margin: 0.2rem;
  padding: 0.2rem;
  color: ${props => props.primary ? 'red' : 'blue'};
`

const CatClaw = styled.div`
  position: relative;
  z-index: 1;
  width: 2rem;
  height: 2rem;
  top: 0;
  left: 0;

  &:hover {

  }
`

const Palm = styled.div`
  position: absolute;
  width: 70%;
  height: 70%;
  border-radius: 100%;
  background: #fff;
  left: 0;
  bottom: 50%;

`


const Ear = styled.div`
  position: absolute;
  z-index: -1;
  background: #000;
  width: 2rem;
  height: 2rem;
`

const LeftEar = styled(Ear)`
  top: -0.8rem;
  left: 0;
  border-radius: 20% 150% 0% 0%;
  animation: moveLeft 2s infinite;

  @keyframes moveLeft {
    0% {
      transform: rotate(-20deg);
      transform-origin: top left;
    }
    50% {
      transform: rotate(-25deg);
      transform-origin: top;
    }
    100% {
      transform: rotate(-20deg);
      transform-origin: top left;
    }
  }
`
const RightEar = styled(Ear)`
  top: -0.8rem;
  right: 0;
  border-radius: 150% 20% 0% 0%;
  animation: moveRight 2s infinite;

  @keyframes moveRight {
    0% {
      transform: rotate(20deg);
      transform-origin: top right;
    }
    50% {
      transform: rotate(25deg);
      transform-origin: top;
    }
    100% {
      transform: rotate(20deg);
      transform-origin: top right;
    }
  }
`

class CatButton extends React.Component {
  render() {
    const to = this.props.to
    return (
      <Button to={to} primary={this.props.primary || false}>
        {this.props.children}
      </Button>
    )
  }
}

export default CatButton



